.nav {
    position: fixed;
    background-color: #f7f7f7 ;
    min-width: 280px;
    min-height: 100vh;
    -webkit-box-shadow: 0px 0px 20px #000000;
            box-shadow: 0px 0px 20px #000000;
    -webkit-transition-duration: 0.3s;
         -o-transition-duration: 0.3s;
            transition-duration: 0.3s;
    left: -300px;   
}

.nav.show {
    left: 0px;
}

.navbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    position: fixed;
    width: 100%;
    z-index: 1;
    background-color: transparent;
}

.navbar p {
    padding-right: 40px;
}

.btn {
    top: 10px;
    left: 15px;
    position: fixed;
    background-color: transparent;
    border: 0;
    font-size: 1.1em;
    z-index: 1;
}

.btn:focus {
    outline: 0;
    background-color: hsla(70, 1, 1, 0.5);
}   

.onShade {
    position: fixed;
    height: 100vh;
    width: 100vw;
    background-color: black;
    opacity: 0.30;
    z-index: -1;
} 




