.button {
    background-color: #ADCCC2; 
    opacity: 0.8;
    border: none;
    color: white;
    font-family: var(--mainFont);
    -webkit-box-shadow: 1px 1px 3px #2F4F4F;
            box-shadow: 1px 1px 3px #2F4F4F;
    margin: 20px;
    font-size: 0.7rem;
    font-style: italic;
    padding: 7px;
    text-transform: uppercase;
    width: 150px;
}

.button:hover {
     opacity: 1;
}
.button:focus {
    outline: none;
    opacity: 1;
}


