
@import '../../config.css'; 

.header {
    background-image:  url('./images/img7.png');
    background-Size: cover;
    background-position: 60% 50%;
    grid-column: 1 / 13;   
    display: -ms-flexbox;   
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    font-size: 16px; 
    position: relative;
}

.title {
    font-family: var(--handwritingFont);
    font-size: 3em;
    color: black ; 
    opacity: 0.7;
    margin: 0px;
    margin-top: 90px;
};

@media (min-width: 768px) {
    .title {
        font-size: 4em;
    }
}

.date {
    font-family: var(--mainFont);
    font-size: 1em;
    color: black ; 
    margin: 0px;
}

@media (min-width: 768px) {
    .date {
        font-size: 1.5em;
    }
}

.enterDiv {
    font-family: var(--mainFont);
    font-size: 1em;
    color: black ; 
    position: absolute;
    left: 0;
    right: 0;
    bottom: 10%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.enterDiv:hover {
    cursor: pointer;
}

@media (min-width: 768px) {
    .enterDiv {
        font-size: 1.5em;
    }
}


.enter {
    margin-bottom: 1%;
}


